:root {
  --danger-color: red;
  --success-color: lightgreen;
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-1px);
  }

  20%, 40%, 60%, 80% {
    transform: translateX(1px);
  }
}

.wrongInput {
  border-color: var(--danger-color);
  animation: .5s 3 shake;
}

.greenSquare {
  background-color: var(--success-color);
  width: 1em;
  height: 1em;
}

kbd {
  color: #656565;
  white-space: nowrap;
  background-color: #f6f6f6;
  border: 1px solid #c5c5c5;
  border-radius: 3px;
  padding: 2px 4px;
  font-size: .85em;
  font-weight: 700;
  line-height: 1;
  display: inline-block;
}

a[shortcut]:before {
  content: "<table style=\"width: 100%;\"><tr><td>";
}

a[shortcut]:after {
  content: "</td><td style=\"text-align: end;\"><kbd>CTRL</kbd> + <kbd>O</kbd></td></tr></table>";
}
